import {mapGetters} from 'vuex';
import appPermissionsAuth from '../../basic-component/app-layout/app-permissions-auth/app-permissions-auth.vue';
import appShareVideoNumber from '../../page-component/app-share-video-number/app-share-video-number.vue';

export default {
    name: 'app-share-qr-code-poster',
    components: {
        appPermissionsAuth,
        appShareVideoNumber
    },
    computed: {
        ...mapGetters('iPhoneX', {
            getBoolEmpty: 'getBoolEmpty'
        })
    },
    props: {
        title: {
            type: String,
            default() {
                return '生成商品海报';
            }
        },
        posterConfig: String,
        posterGenerate: String,
        hasPosterNav: {
            type: Boolean,
            default() {
                return false
            },
        },
        value: {
            type: Boolean,
            default() {
                return false;
            }
        },
        url: {
            type: String,
            default() {
                return '';
            }
        },
        disabled: {
            type: Boolean,
            default() {
                return false;
            }
        },
        isHidden: {
            type: Boolean,
            default() {
                return true;
            }
        },
        posterUrl: {
            type: String,
            default() {
                return ''
            }
        },
        theme: [Object,String],
        sign: String,
        isShowFriend: {
            type: Boolean,
            default() {
                return true;
            }
        },
        appShareTitle: String,
        appSharePic: String,
        goods: {
            type: Object,
            default() {
                return {}
            }
        }
    },
    data() {
        return {
            showHidden: this.value,
            buttonBoolean: true,
            shareImage: '',
            is_can_save: false,
            text: '保存图片需要您的授权。点击去设置,开启 保存到相册 权限',
            loading: true,

            isShowVideoNumber: false,
            article_url: '',
            number: 1,
            isClick: true,
            isShow: false,
        }
    },
    methods: {
        fenxianghaibao() {
            if (this.hasPosterNav) {
                let url = this.posterUrl;
                if (this.posterUrl == '') {

                    url = `/pages/poster/goods?poster_config=${escape(this.posterConfig)}&poster_generate=${escape(this.posterGenerate)}`




                }
                uni.navigateTo({
                    url: url
                });
            } else {
                this.buttonBoolean = false;
                this.getShareImg();
            }
        },
        showHiddenClick() {
            this.showHidden = false;
            this.$emit('input', this.showHidden);
            this.buttonBoolean = true;
        },
        savePicture() {
            if (this.loading) return;
            this.$utils.batchSave(this.shareImage, 'image').then(() => {
                uni.showToast({title: '保存成功'});
            });
        },
        getShareImg() {
            if (!this.url) return;
            this.$request({
                url: this.url,
            }).then(response => {
                if (response.code === 0) {
                    this.shareImage = response.data.pic_url;
                    this.loading = false;
                } else {
                    uni.showModal({
                        content: response.msg,
                        showCancel: false,
                    });
                }
            });
        },
        getAuthSetting() {
            let self = this;
            uni.getSetting({
                success(res) {

                    self.is_can_save = !res.authSetting['scope.writePhotosAlbum'];




                }
            });
        },
        cancel(isShow) {
            this.is_can_save = isShow;
        },
        preview(data) {
            uni.previewImage({
                urls: [data],
                longPressActions: true,
            });
        },








        videoNumberClick() {
            if (!this.isClick) {
                return
            }
            this.isClick = false;

            uni.showLoading({title: '加载中'});
            this.$request({
                url: this.$api.goods.addMaterial,
                data: {
                    goods_id: this.goods.id
                }
            }).then(response => {
                uni.hideLoading();
                if (response.code === 0) {
                    this.isShowVideoNumber = true;
                    this.getArticleUrl(response.data.media_id);
                } else {
                    this.resetVideoNumber()
                    uni.showModal({
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                this.resetVideoNumber()
            });
        },
        getArticleUrl(mediaId) {
            let interval = setInterval(() => {
                if (this.article_url) {
                    clearInterval(interval);
                }
                if (this.number > 30) {
                    clearInterval(interval);
                    this.resetVideoNumber()
                    uni.showModal({
                        content: '获取视频号链接异常,请稍后重试',
                        showCancel: false,
                    });
                }

                this.number++;
                this.$request({
                    url: this.$api.goods.articleUrl,
                    data: {
                        media_id: mediaId
                    }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.article_url = response.data.url;
                    } else {
                        this.resetVideoNumber()
                        uni.showModal({
                            content: response.msg,
                            showCancel: false,
                        });
                        clearInterval(interval);
                    }
                });
            }, 1000)
        },
        resetVideoNumber() {
            this.number = 1;
            this.isShowVideoNumber = false;
            this.isClick = true;
        },
        copyText() {
            this.$utils.uniCopy({
                data: this.article_url,
                success() {
                    uni.showToast({title: '复制成功'});
                    this.resetVideoNumber()
                }
            });
        },
        closeVideoNumber() {
            this.isShowVideoNumber = false;
        },








    }
}
